$n: transparent;
$black: #0c0c0c;
$dark: #282626;
$white: #f6f6f6;
$red: #94251d;
$yellow: #ffbd00;
$gray: #e9e9e9;


@include font('helveticaneueltcyr-bd', 'helveticaneueltcyr-bd');
@include font('HeliBla', 'HeliBla');
@include font('heliosregular-webfont', 'heliosregular-webfont');

@mixin hb {
	font-family: 'helveticaneueltcyr-bd', sans-serif; }

@mixin hr {
	font-family: 'heliosregular-webfont', sans-serif; }
@mixin hbl {
	font-family: 'HeliBla', sans-serif; }

@mixin mr {
	font-family: 'Montserrat', sans-serif;
	font-weight: 400; }

@mixin mb {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700; }

@mixin meb {
	font-family: 'Montserrat', sans-serif;
	font-weight: 800; }

@mixin mbl {
	font-family: 'Montserrat', sans-serif;
	font-weight: 900; }


.title {
	&__main {
		text-transform: uppercase;
		color: #fff;
		font-size: 30px;
		line-height: 1.2em;
		@include mbl;
		@include r(1024) {
			font-size: 24px; }
		mark {
			color: #fff;
			background-color: $red;
			padding: 0 8px; } }
	&__hero {
		position: relative;
		font-size: 50px;
		text-transform: uppercase;
		margin-bottom: 0;
		@include mbl;
		@include r(1200) {
			font-size: 42px; }
		@include r(1100) {
			font-size: 34px; }
		@include r(800) {
			font-size: 24px; }
		@include r(340) {
			font-size: 22px; }
		mark {
			background-color: $n;
			color: $red; } }

	&__h {
		font-size: 24px;
		color: $dark;
		@include mr; }
	&__p {
		font-size: 20px;
		text-transform: uppercase;
		color: $black;
		@include meb;
		@include r(1024) {
			font-size: 16px; }
		mark {
			@include mbl; } }
	&__l {
		font-size: 18px;
		color: #0c0c0c;
		@include meb;
		@include r(1024) {
			font-size: 14px; } }
	&__come {
		font-size: 18px;
		text-transform: uppercase;
		color: #fff;
		@include meb;
		@include r(1024) {
			font-size: 14px; } }
	&__d {
		font-size: 16px;
		color: $black;
		margin-bottom: .8em;
		@include mbl; }
	&__ac {
		font-size: 16px;
		color: $black;
		@include hbl; } }

.desc {
	&__ac {
		font-size: 14px;
		color: #353535;
		@include hr; }
	&__r {
		font-size: 14px;
		line-height: 1.6;
		color: #0c0c0c;
		margin-bottom: 1.2em;
		@include mr;
		mark {
			color: $red;
			background-color: $n;
			@include mb; } } }

.btn {
	display: inline-block;
	vertical-align: top;
	padding: 12px 20px;
	background-color: $yellow;
	color: $black;
	border-radius: 4px;
	box-shadow: 0px 10px 15px 0px rgba(255, 189, 0, 0.25);
	text-transform: uppercase;
	font-size: 14px;
	@include mbl;
	@include tr(.4s);
	&:hover {
		color: #fff;
		box-shadow: inset 0 0 4px rgba(255, 255, 255, .2), inset 0 0 4px rgba(0, 0, 0, .2), 0px 10px 15px 0px rgba(255, 189, 0, 0.25); }
	&-close {
		width: 20px;
		height: 20px;
		display: inline-block;
		position: absolute;
		top: 14px;
		right: 14px;
		&:before {
			content: '';
			position: absolute;
			top: 3px;
			left: 4px;
			width: 18px;
			height: 2px;
			background-color: #e9e9e9;
			@include rotate(45);
			transform-origin: 0 0; }
		&:after {
			content: '';
			position: absolute;
			top: 16px;
			left: 2px;
			width: 18px;
			height: 2px;
			background-color: #e9e9e9;
			@include rotate(-45);
			transform-origin: 0 0; }
		&:hover {
			background-color: #ccc; } } }

.icon-loc {
	display: inline-block;
	vertical-align: top;
	height: 22px;
	fill: #292929;
	@include r(1024) {
		fill: #fff; } }
