.hero {
	padding: 0;
	position: relative;
	// min-height: 90vh
	@include r(1024) {
		background-color: #07080c; }
	&__pic {
		padding: 0;
		margin: 0;
		font-size: 0;

		height: 100%;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 50%;
		@include r(1200) {
			left: 40%; }
		@include r(1024) {
			right: -10%;
			left: auto; }
		img {
			height: 100%;
			max-width: none; } }
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		width: 100%;
		@include r(1024) {
			width: auto; } }
	&__dates {
		display: block;
		width: 100%;
		color: $red;
		font-size: 16px;
		@include mbl; }
	&__location {
		margin-bottom: auto;
		margin-top: 40px;
		@include r(800) {
			margin-top: 30px;
			margin-bottom: 40px; }
		@include r(340) {
			margin-top: 14px; } }
	&__skew {
		position: relative;
		transform: translate3d(30px, 0, 0) skew(18deg);
		background-color: #d1d1d1;
		border-left: 2px solid $yellow;
		padding: 10px 0;
		@include r(800) {
			transform: translate3d(10px, 0, 0) skew(18deg); }
		@include r(340) {
			transform: translate3d(5px, 0, 0) skew(18deg); }
		&:before {
			content: '';
			@include coverdiv;
			width: 140%;
			background-color: #d1d1d1;
			@include r(1024) {
				content: none; } } }
	&__unskew {
		transform: translate3d(0, 0, 0) skew(-18deg);
		margin-left: 10px;
		@include r(1024) {
			padding-right: 30px; } }
	&__btn {
		margin-top: 60px;
		margin-bottom: auto;
		@include r(800) {
			margin-top: 120px; } }

	.justified {
		position: relative;
		height: 90vh; }
	.justified__item {
		position: relative;
		width: 50%;
		@include r(1024) {
			width: 100%;
			justify-content: flex-start; }
		&.w-60 {
			@include r(1024) {
				width: 100%; } } }
	.layout {
		position: relative;
		@include r(1024) {
			z-index: 1; } }
	.title__h {
		color: #fff; }

	.w-60 {
		width: 60%; } }

.scroll-down {
	position: absolute;
	bottom: -1px;
	left: 50%;
	padding: 6px 10px;
	transform: translate3d(-50%, 0%, 0) skew(15deg);
	white-space: nowrap;
	z-index: 5;
	@include tr(.4s);
	@include r(800) {
		background-color: #d1d1d1; }
	&:hover {
		background-color: lighten(#d1d1d1, 10%); }
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		width: 4px;
		@include g(#e7e7e7, #494949); }
	&:after {
		left: 100%; }
	&__wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		transform: translate3d(0, 0%, 0) skew(-15deg);
		.desc__r {
			margin: 0;
			padding: 0;
			height: 100%;
			// +r(1024)
 } } }			// 	color: #fff


@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) and (-webkit-min-device-pixel-ratio: 2) {
	.hero__pic {
		right: -44%; }
	.hero {
		.layout {
			position: relative;
			z-index: 1; }
		.scroll-down {
			background-color: #e7e7e7; } } }

@media only screen and (orientation : landscape) and (-webkit-min-device-pixel-ratio: 2) {
	.hero__pic {
		right: -20%;
		left: auto; }
	.hero {
		.layout {
			position: relative;
			z-index: 1; }
		.scroll-down {
			background-color: #e7e7e7; } }
	.hero__skew {
		&:before {
			content: none; } } }
